.card {
  /* max-width: 400px; */
  width: 100%;
  height: 100%;
  /* height: 300px; */
  /* border-radius: 10px;
  border: 2px solid black; */
  overflow: hidden;
  transition: all 0.5s ease-in-out;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  position: relative;
  box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
}

.card_img {
  object-fit: cover;
  width: 100%;
  height: 300px;
  overflow: hidden;
}
.card_description {
  flex-direction: column;
  display: flex;
  gap: 1rem;
  align-self: center;
  align-items: center;
  text-align: center;
  position: absolute;
  opacity: 0;
  transform: translateY(-5%);
  height: 100%;
  width: 100%;
  transition: all 0.3s ease-in-out;
}
.card_description_title {
  font-family: 'Zen Dots', sans-serif;
  font-size: 25px;
  font-weight: 800;
  transition: all 0.3s ease-in-out;
  text-decoration: none;
  color: black;
}
.card_description_content {
  font-family: 'Righteous', sans-serif;
  color: rgb(82, 82, 82);
  font-size: 14px;
}

.card_technologie {
  display: flex;
  gap: 1rem;
}
.card_technologie_logo {
  width: 50px;
}

.card:hover {
  transform: scale(1.05);
}

.card_description_title:hover {
  background-image: linear-gradient(
    90deg,
    rgba(255, 0, 77, 1) 0%,
    rgba(0, 255, 209, 1) 100%
  );
  -webkit-background-clip: text;
  -webkit-text-fill-color: #00000000;
  cursor: pointer;
}

.card_description:hover {
  transform: translateY(0px);
  justify-content: center;
  opacity: 1;
  background: rgba(255, 255, 255, 0.75);
}
